<template>
  <v-container fluid class="pa-0">
    <v-row>
      <v-col cols="12">
        <div
          class="d-flex justify-space-between align-center py-2 header-container"
        >
          <h2 class="text-h4 font-weight-bold page-title">Party Members</h2>
          <div class="d-flex align-center gap-4 button-group">
            <v-btn
              @click="addParty"
              variant="tonal"
              color="#1f9ee5"
              prepend-icon="mdi-plus"
            >
              Add Party
            </v-btn>
            <v-btn
              @click="saveParties"
              variant="tonal"
              color="success"
              prepend-icon="mdi-content-save"
              :loading="saving"
              class="mx-4"
            >
              Save Parties
            </v-btn>
          </div>
        </div>
        <hr />
      </v-col>
    </v-row>

    <v-row>
      <!-- Unassigned Users Pool -->
      <v-col cols="12" md="12" lg="3">
        <v-card class="party-card" id="user-pool-card">
          <v-card-title class="text-center">Available Users</v-card-title>
          <v-card-text>
            <draggable
              v-if="!isMobile"
              v-model="unassignedUsers"
              group="users"
              item-key="username"
              class="user-pool"
            >
              <template #item="{ element }">
                <v-card class="user-card mb-2">
                  <v-card-text class="d-flex align-center">
                    <div class="d-flex flex-column">
                      <div class="d-flex align-center">
                        <v-icon color="#1f9ee5" class="me-2"
                          >mdi-account</v-icon
                        >
                        {{ element.username }}
                      </div>
                      <div
                        class="user-details"
                        v-if="element.class || element.level"
                      >
                        <span v-if="element.class">{{ element.class }}</span>
                        <span v-if="element.class && element.level">  </span>
                        <span v-if="element.level"
                          >Level {{ element.level }}</span
                        >
                      </div>
                    </div>
                  </v-card-text>
                </v-card>
              </template>
            </draggable>

            <!-- Mobile Non-Draggable Version -->
            <div v-else class="user-pool-mobile">
              <v-card
                v-for="element in unassignedUsers"
                :key="element.username"
                class="user-card mb-2"
              >
                <v-card-text class="d-flex align-center justify-space-between">
                  <div class="d-flex align-center">
                    <v-icon color="#1f9ee5" class="me-2">mdi-account</v-icon>
                    <div>
                      <div class="user-name">{{ element.username }}</div>
                      <div
                        v-if="element.class || element.level"
                        class="user-details"
                      >
                        <span v-if="element.class">{{ element.class }}</span>
                        <span v-if="element.class && element.level">  </span>
                        <span v-if="element.level"
                          >Level {{ element.level }}</span
                        >
                      </div>
                    </div>
                  </div>
                  <v-btn
                    icon
                    size="small"
                    color="#1f9ee5"
                    variant="outlined"
                    @click="openMoveDialog(element, 'unassigned')"
                  >
                    <v-icon>mdi-arrow-right</v-icon>
                  </v-btn>
                </v-card-text>
              </v-card>
              <div v-if="unassignedUsers.length === 0" class="empty-state">
                <v-icon color="#1f9ee5" size="48">mdi-account-group</v-icon>
                <p class="text-center mt-2">No available users</p>
              </div>
            </div>
          </v-card-text>
        </v-card>
      </v-col>

      <!-- Party Groups -->
      <v-col cols="12" md="12" lg="9">
        <v-row style="width: 100%">
          <v-col
            v-for="(party, index) in parties"
            :key="index"
            cols="12"
            sm="6"
            lg="4"
          >
            <v-card class="party-card">
              <v-card-title class="text-center party-title-container">
                <div class="d-flex align-center justify-space-between w-100">
                  <v-text-field
                    v-model="party.name"
                    variant="plain"
                    class="party-name-field"
                    hide-details
                    density="compact"
                    @click.stop
                  />
                  <div class="d-flex align-center">
                    <span class="party-count me-2"
                      >({{ party.members.length }})</span
                    >
                    <v-btn
                      icon
                      size="small"
                      variant="text"
                      color="error"
                      @click="deleteParty(index)"
                    >
                      <v-icon size="medium">mdi-close</v-icon>
                    </v-btn>
                  </div>
                </div>
              </v-card-title>
              <v-card-text>
                <draggable
                  v-if="!isMobile"
                  v-model="party.members"
                  group="users"
                  item-key="username"
                  class="party-drop-zone"
                >
                  <template #item="{ element }">
                    <v-card class="user-card mb-1">
                      <v-card-text class="d-flex align-center">
                        <div class="d-flex flex-column">
                          <div class="d-flex align-center">
                            <v-icon color="#1f9ee5" class="me-2"
                              >mdi-account</v-icon
                            >
                            {{ element.username }}
                          </div>
                          <div
                            class="user-details"
                            v-if="element.class || element.level"
                          >
                            <span v-if="element.class">{{
                              element.class
                            }}</span>
                            <span v-if="element.class && element.level">
                              
                            </span>
                            <span v-if="element.level"
                              >Level {{ element.level }}</span
                            >
                          </div>
                        </div>
                      </v-card-text>
                    </v-card>
                  </template>
                </draggable>

                <!-- Mobile Non-Draggable Version -->
                <div v-else class="party-drop-zone-mobile">
                  <v-card
                    v-for="element in party.members"
                    :key="element.username"
                    class="user-card mb-1"
                  >
                    <v-card-text
                      class="d-flex align-center justify-space-between"
                    >
                      <div class="d-flex align-center">
                        <v-icon color="#1f9ee5" class="me-2"
                          >mdi-account</v-icon
                        >
                        <div>
                          <div class="user-name">{{ element.username }}</div>
                          <div
                            v-if="element.class || element.level"
                            class="user-details"
                          >
                            <span v-if="element.class">{{
                              element.class
                            }}</span>
                            <span v-if="element.class && element.level">
                              
                            </span>
                            <span v-if="element.level"
                              >Level {{ element.level }}</span
                            >
                          </div>
                        </div>
                      </div>
                      <v-btn
                        icon
                        size="small"
                        color="#1f9ee5"
                        variant="outlined"
                        @click="openMoveDialog(element, party.id)"
                      >
                        <v-icon>mdi-arrow-right</v-icon>
                      </v-btn>
                    </v-card-text>
                  </v-card>
                  <div v-if="party.members.length === 0" class="empty-state">
                    <v-icon color="#1f9ee5" size="48">mdi-account-plus</v-icon>
                    <p class="text-center mt-2">No party members</p>
                  </div>
                </div>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-col>
    </v-row>

    <!-- Move User Dialog (Mobile) -->
    <v-dialog v-model="showMoveDialog" max-width="400px">
      <v-card class="mobile-move-dialog" theme="dark">
        <v-card-title class="d-flex justify-space-between align-center">
          <span>Move {{ selectedUser?.username }}</span>
          <v-btn
            icon="mdi-close"
            variant="text"
            @click="showMoveDialog = false"
          ></v-btn>
        </v-card-title>

        <v-card-text>
          <div class="current-location mb-3">
            <v-chip color="#1f9ee5" variant="outlined" size="small">
              Currently in: {{ getCurrentLocationName() }}
            </v-chip>
          </div>

          <v-list>
            <v-list-item
              v-for="option in moveOptions"
              :key="option.value"
              @click="moveUserToParty(option.value)"
              class="move-option"
              :class="{
                'current-location-option': option.value === currentUserLocation,
              }"
            >
              <template v-slot:prepend>
                <v-icon color="#1f9ee5">
                  {{
                    option.value === "unassigned"
                      ? "mdi-account-group"
                      : "mdi-account-multiple"
                  }}
                </v-icon>
              </template>
              <v-list-item-title>{{ option.title }}</v-list-item-title>
              <template
                v-slot:append
                v-if="option.value === currentUserLocation"
              >
                <v-chip size="x-small" color="success">Current</v-chip>
              </template>
            </v-list-item>
          </v-list>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script setup>
import { ref, defineProps, watch, inject, onMounted, onUnmounted } from "vue";
import {
  ref as dbRef,
  set,
  get,
  query,
  orderByKey,
  limitToLast,
  remove,
} from "firebase/database";
import draggable from "vuedraggable";

const props = defineProps({
  users: {
    type: Array,
    default: () => [],
  },
});

// Get database instance
const db = inject("db");
const saving = ref(false);

const unassignedUsers = ref([]);
const parties = ref([
  { id: 1, name: "Party 1", members: [] },
  { id: 2, name: "Party 2", members: [] },
  { id: 3, name: "Party 3", members: [] },
  { id: 4, name: "Party 4", members: [] },
]);

// Mobile detection and dialog state
const isMobile = ref(false);
const showMoveDialog = ref(false);
const selectedUser = ref(null);
const currentUserLocation = ref(null);
const moveOptions = ref([]);

// Load latest party configuration from database
const loadLatestPartyConfiguration = async (allUsers) => {
  if (!db) {
    // If no database, just put everyone in unassigned
    unassignedUsers.value = allUsers;
    return;
  }

  try {
    const partiesRef = dbRef(db, "parties");
    const latestQuery = query(partiesRef, orderByKey(), limitToLast(1));
    const snapshot = await get(latestQuery);

    if (snapshot.exists()) {
      const data = snapshot.val();
      const latestConfig = Object.values(data)[0]; // Get the most recent entry

      console.log("Loading saved party configuration:", latestConfig);

      // Create a map of saved users for quick lookup
      const savedUserMap = new Map();

      // Add unassigned users to map
      latestConfig.unassignedUsers?.forEach((user) => {
        savedUserMap.set(user.username, { ...user, assignment: "unassigned" });
      });

      // Add party members to map
      latestConfig.parties?.forEach((party) => {
        party.members?.forEach((user) => {
          savedUserMap.set(user.username, { ...user, assignment: party.id });
        });
      });

      // Update parties structure to match saved configuration
      if (latestConfig.parties && latestConfig.parties.length > 0) {
        parties.value = latestConfig.parties.map((party) => ({
          id: party.id,
          name: party.name || `Party ${party.id}`,
          members: [],
        }));
      }

      // Distribute users based on saved configuration
      const newUnassigned = [];
      const partyMembers = {};

      // Initialize party members arrays
      parties.value.forEach((party) => {
        partyMembers[party.id] = [];
      });

      allUsers.forEach((user) => {
        const savedUser = savedUserMap.get(user.username);
        if (savedUser && savedUser.assignment !== "unassigned") {
          // User was in a party
          if (partyMembers[savedUser.assignment]) {
            partyMembers[savedUser.assignment].push(user);
          } else {
            // Party doesn't exist anymore, put in unassigned
            newUnassigned.push(user);
          }
        } else {
          // User was unassigned or not found in saved config
          newUnassigned.push(user);
        }
      });

      // Apply the distribution
      unassignedUsers.value = newUnassigned;
      parties.value.forEach((party) => {
        party.members = partyMembers[party.id] || [];
      });

      console.log("Party configuration loaded successfully");
    } else {
      console.log("No saved party configuration found, using default");
      unassignedUsers.value = allUsers;
    }
  } catch (error) {
    console.error("Error loading party configuration:", error);
    // Fallback to putting everyone in unassigned
    unassignedUsers.value = allUsers;
  }
};

// Convert users prop to the format needed for drag and drop
const populateUsers = async () => {
  const allUsers = props.users
    .map((user) => ({
      username: user.user,
      class: user.class || "",
      level: user.level || "",
    }))
    .sort((a, b) => a.username.localeCompare(b.username));

  // Try to load saved party configuration
  await loadLatestPartyConfiguration(allUsers);
};

// Watch for changes in users prop
watch(
  () => props.users,
  async () => {
    await populateUsers();
  },
  { immediate: true }
);

// Ensure we load configuration when component mounts
onMounted(async () => {
  if (props.users.length > 0) {
    await populateUsers();
  }
});

// Function to add a new party group
const addParty = () => {
  const newId = Math.max(...parties.value.map((p) => p.id)) + 1;
  parties.value.push({
    id: newId,
    name: `Party ${newId}`,
    members: [],
  });
};

// Function to delete a party and move its members to unassigned (local only)
const deleteParty = (partyIndex) => {
  const partyToDelete = parties.value[partyIndex];

  // Move all party members to unassigned users
  unassignedUsers.value.push(...partyToDelete.members);

  // Remove the party from the array
  parties.value.splice(partyIndex, 1);

  console.log(
    `Party "${partyToDelete.name}" deleted locally. Click Save to persist changes.`
  );
};

// Function to clean up old party entries (older than 2 weeks)
const cleanupOldPartyEntries = async () => {
  if (!db) {
    console.error("Database not available for cleanup");
    return;
  }

  try {
    const partiesRef = dbRef(db, "parties");
    const snapshot = await get(partiesRef);

    if (snapshot.exists()) {
      const data = snapshot.val();
      const twoWeeksAgo = new Date();
      twoWeeksAgo.setDate(twoWeeksAgo.getDate() - 14);

      const keysToDelete = [];

      // Check each entry to see if it's older than 2 weeks
      Object.keys(data).forEach((key) => {
        const entry = data[key];
        if (entry.timestamp) {
          const entryDate = new Date(entry.timestamp);
          if (entryDate < twoWeeksAgo) {
            keysToDelete.push(key);
          }
        }
      });

      // Delete old entries
      for (const key of keysToDelete) {
        const entryRef = dbRef(db, `parties/${key}`);
        await remove(entryRef);
      }

      if (keysToDelete.length > 0) {
        console.log(`Cleaned up ${keysToDelete.length} old party entries`);
      }
    }
  } catch (error) {
    console.error("Error cleaning up old party entries:", error);
  }
};

// Function to save party configuration to database
const saveParties = async () => {
  if (!db) {
    console.error("Database not available");
    return;
  }

  saving.value = true;
  try {
    const now = new Date();
    const timestamp = now.toISOString();
    // Create Firebase-safe key by replacing invalid characters
    const firebaseKey = timestamp.replace(/[.#$[\]]/g, "_");

    const partyData = {
      timestamp,
      unassignedUsers: unassignedUsers.value,
      parties: parties.value.map((party) => ({
        id: party.id,
        name: party.name,
        members: party.members,
        memberCount: party.members.length,
      })),
    };

    const partiesRef = dbRef(db, `parties/${firebaseKey}`);
    await set(partiesRef, partyData);

    // Clean up old party entries (older than 2 weeks)
    await cleanupOldPartyEntries();

    console.log("Parties saved successfully!", partyData);
    // You could add a success notification here
  } catch (error) {
    console.error("Error saving parties:", error);
    // You could add an error notification here
  } finally {
    saving.value = false;
  }
};

// Check if the device is mobile
const checkMobile = () => {
  isMobile.value = window.innerWidth < 768;
};

// Open the move dialog for a user
const openMoveDialog = (user, currentLocation) => {
  selectedUser.value = user;
  currentUserLocation.value = currentLocation;

  // Create move options excluding current location
  moveOptions.value = [
    { title: "Available Users", value: "unassigned" },
    ...parties.value.map((party) => ({
      title: party.name,
      value: party.id,
    })),
  ];

  showMoveDialog.value = true;
};

// Get current location name for display
const getCurrentLocationName = () => {
  if (currentUserLocation.value === "unassigned") {
    return "Available Users";
  }
  const party = parties.value.find((p) => p.id === currentUserLocation.value);
  return party ? party.name : "Unknown";
};

// Move the user to a selected party
const moveUserToParty = (targetLocation) => {
  if (!selectedUser.value || targetLocation === currentUserLocation.value) {
    showMoveDialog.value = false;
    return;
  }

  const user = selectedUser.value;

  // Remove user from current location
  removeUserFromAllParties(user);

  // Add to target location
  if (targetLocation === "unassigned") {
    unassignedUsers.value.push(user);
  } else {
    const targetParty = parties.value.find((p) => p.id === targetLocation);
    if (targetParty) {
      targetParty.members.push(user);
    }
  }

  showMoveDialog.value = false;
  selectedUser.value = null;
  currentUserLocation.value = null;
};

// Remove a user from all parties and unassigned list
const removeUserFromAllParties = (user) => {
  // Remove from unassigned
  const unassignedIndex = unassignedUsers.value.findIndex(
    (u) => u.username === user.username
  );
  if (unassignedIndex > -1) {
    unassignedUsers.value.splice(unassignedIndex, 1);
    return;
  }

  // Remove from parties
  parties.value.forEach((party) => {
    const userIndex = party.members.findIndex(
      (u) => u.username === user.username
    );
    if (userIndex > -1) {
      party.members.splice(userIndex, 1);
    }
  });
};

onMounted(() => {
  checkMobile();
  window.addEventListener("resize", checkMobile);
});

onUnmounted(() => {
  window.removeEventListener("resize", checkMobile);
});
</script>

